.page {
  width: 100%;
  height: 100%;
  position: absolute;
  // position: relative;
  margin-right: 2rem;
  padding-right: 3rem;
}
.top-tags {
  bottom: auto;
  top: 35px;
}
.tags {
  color: #ffd700;
  opacity: 0.6;
  position: absolute;
  bottom: 0;
  left: 120px;
  font-size: 18px;
  font-family: 'La Belle Aurore';
}

.bottm-tag-html {
  margin-left: -20px;
}
.container {
  width: 100%;
  will-change: contents;
  height: 90%;
  min-height: 566px;
  // position: absolute;
  position: relative;
  opacity: 0;
  top: 5%;
  margin: 0 auto;
  z-index: 1;
  transform-style: preserve-3d;
  animation: fadeIn 1s forwards;
  animation-delay: 1sec;
}
.container.about-page,
.container.contact-page,
.container.portfolio-page {
  h1 {
    font-size: 5rem;
    font-family: 'Coolvetica';
    color: #ffd700;
    font-weight: 400;
    margin-top: 0;
    position: relative;
    margin-bottom: 40px;
    left: 10px;
  }
  p {
    font-style: 13px;
    color: #fff;
    font-family: sans-serif;
    font-weight: 300;
    min-width: fit-content;
    animation: pulse 1s;
    &:nth-of-type(1) {
      animation-delay: 1.1s;
    }
    &:nth-of-type(2) {
      animation-delay: 1.2s;
    }
    &:nth-of-type(3) {
      animation-delay: 1.3s;
    }
  }
  .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    // display: table-cell;
  }
  .text-animate-hover {
    &:hover {
      color: #fff;
    }
  }
}

@media only screen and (max-width: 1200px) {
  .container.about-page,
  .container.contact-page,
  .container.portfolio-page {
    h1 {
      font-size: 4rem;
    }
  }
}
